.group-card{
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-items: center;
    width: 90%;
    margin: 0;
}

.card-skill{
    display: grid;
    grid-template-columns: 1fr 2fr;
    justify-items: center;
    width: 90%;
    padding: 20px 20px;
    box-shadow: $box-shadow;
    border-radius: 20px;
}

.img-card{
    display: flex;
    align-items: center;
    width: 60%;
}

.title-card{
    color: $font-text;
    font-size: 2rem;
}

.text-card{
    color: $font-text;
    font-size: 1.4rem;
}

.logo-empresa{
    width: 20rem !important
}